@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
#navbar {
  width: 100vw;
  background: var(--white);
  display: flex;
  justify-content: space-between;
  color: var(--grey);
  align-items: center;
  padding: 0.25rem;
  position: -webkit-sticky;
  position: sticky;
  z-index: 1;
  top: 0;
}

#navbar .logo {
  display: flex;
  align-items: center;
}

#navbar #logo-img {
  display: block;
  width: 18%;
  padding: 0.4rem;
}

#navbar ul {
  display: flex;
  margin-right: 0.6rem;
}

#navbar ul li a {
  color: black;
  font-weight: 500;
  padding: 1rem;
}

#navbar ul li a:hover {
  opacity: 0.7;
}

/* smartphones */
@media (max-width: 768px) {
  #navbar {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  #navbar .logo {
    justify-content: center;
  }

  #navbar #logo-img {
    display: block;
    width: 15%;
    padding: 0.1rem;
  }

  #navbar ul {
    margin-right: 0rem;
  }

  #navbar ul li a {
    color: black;
    font-weight: 500;
    padding: 0.2rem;
  }
}

#showcase {
  display: flex;
  background: url(/nordvpn-clone/static/media/header-showcase.dfe70d17.jpg) no-repeat center center/cover;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

#showcase .offer-details {
  padding-left: 2rem;
}

#showcase .main-heading {
  font-size: 4.5rem;
  margin-bottom: 3rem;
  color: var(--white);
}

#showcase .deal-btn {
  color: var(--white);
  font-size: 1rem;
  display: inline-block;
  font-weight: 600;
  background: #f64f64;
  padding: 0.8rem 1.5rem;
  border-radius: 5px;
}
#showcase .deal-btn:hover {
  background: #fa95a2;
}

#showcase .offer-img {
  padding: 1rem;
}

#showcase p {
  padding-top: 1rem;
  color: var(--white);
}

@media (max-width: 768px) {
  #showcase .offer-img {
    display: none;
  }

  #showcase .offer-details {
    padding: 0;
    text-align: center;
  }

  #showcase .main-heading {
    font-size: 4rem;
    margin-bottom: 5rem;
  }

  #showcase .deal-btn {
    font-size: 1.2rem;
    padding: 1rem 2rem;
  }

  #showcase p {
    padding-top: 2rem;
    font-size: 1.2rem;
  }
}

#features {
  display: flex;
  padding: 0 6rem;
  /* border: 1px green solid; */
}

#features .features-info {
  padding-top: 5rem;
}

#features .features-info p {
  width: 70%;
  padding: 1.5rem 0;
}

#features .features-img img {
  display: block;
  width: 100%;
}

#features .features-img {
  /* margin-bottom: 10rem;  eski vaja se hua tha sab padding vala panga*/
}

#features .features-info,
#features .features-img {
  flex: 1 1;
}

@media (max-width: 768px) {
  #features {
    flex-direction: column-reverse;
    padding: 0;
    text-align: center;
  }

  #features .features-info {
    padding-top: 0rem;
  }

  #features .features-info p {
    width: 100%;
    padding: 1.5rem 0.5rem 1.5rem 0.5rem;
  }

  #features .btn-secondary {
    display: block;
    width: 90%;
    margin: auto;
    padding: 0.75rem 1.5rem;
    font-size: 1.1rem;
  }
}

#privacy {
  display: flex;
  padding: 0 6rem;
  /* border: 1px red solid; */
}

#privacy .privacy-info,
#privacy .privacy-img {
  flex: 1 1;
}

#privacy .privacy-info {
  padding-top: 5rem;
  padding-left: 7rem;
  width: 50%;
  /* background:red; */
}

#privacy .privacy-info p {
  width: 100%;
  padding: 1.5rem 0rem;
}

#privacy .privacy-img img {
  display: block;
  width: 100%;
}

.available-platforms {
  display: flex;
  margin-bottom: 1.5rem;
  /* border: 1px solid red; */
}

.available-platforms i {
  padding: 0 0.5rem;
  font-size: 1.3rem;
  color: #383c43;
}
.available-platforms i:hover {
  color: #fa95a2;
}

@media (max-width: 768px) {
  #privacy {
    flex-direction: column;
    padding: 0;
    text-align: center;
    margin-top: 2rem;
  }

  #privacy .privacy-info {
    padding: 0;
    width: 100%;
  }

  #privacy .privacy-info p {
    width: 100%;
    padding: 1.5rem 0.5rem 1.5rem 0.5rem;
  }

  #privacy .btn-secondary {
    display: block;
    width: 90%;
    margin: auto;
    padding: 0.75rem 1.5rem;
    font-size: 1.1rem;
  }

  #privacy .available-platforms {
    display: flex;
    justify-content: center;
  }
}

#performance {
  display: flex;
  padding: 0 6rem;
  /* border: 1px green solid; */
}

#performance .performance-info {
  padding-top: 5rem;
}

#performance .performance-info p {
  width: 70%;
  padding: 1.5rem 0rem;
}

#performance .performance-img img {
  display: block;
  width: 100%;
}

#performance .performance-img {
  /* margin-bottom: 10rem;  eski vaja se hua tha sab padding vala panga*/
}

#performance .performance-info,
#performance .performance-img {
  flex: 1 1;
}

@media (max-width: 768px) {
  #performance {
    flex-direction: column-reverse;
    padding: 0;
    margin-top: 3rem;
    /* margin-top extra add kiya he bss baki ye aur features bilkul same he */
    text-align: center;
  }

  #performance .performance-info {
    padding-top: 0rem;
  }

  #performance .performance-info p {
    width: 100%;
    padding: 1.5rem 0.5rem 1.5rem 0.5rem;
  }

  #performance .btn-secondary {
    display: block;
    width: 90%;
    margin: auto;
    padding: 0.75rem 1.5rem;
    font-size: 1.1rem;
  }
}

#reviews {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-top: 4rem;
}

#reviews .reviews-info h2 {
  color: var(--grey);
  font-size: 2.5rem;
}

#reviews .reviews-info p {
  margin-top: 0.5rem;
}

#reviews .reviews-imgs {
  display: flex;
  align-items: center;
  justify-content: center;
}

#reviews .reviews-imgs div {
  margin: 2rem 0.8rem;
  height: 25rem;
  width: 22rem;
  flex: 1 1;
  display: flex;
  flex-direction: column;
}

#reviews .reviews-imgs div:hover {
  opacity: 0.9;
}

#reviews .reviews-imgs div img {
  display: block;
  width: 100%;
  flex: 1 1;
}

#reviews .reviews-imgs p {
  flex: 2 1;
  color: white;
  background: #2a2d30;
  text-align: start;
  padding: 1rem;
}

#reviews .reviews-imgs p .review-author {
  font-size: 0.7rem;
}

@media (max-width: 768px) {
  #reviews {
    margin-top: 4rem;
  }

  #reviews .reviews-info h2 {
    color: var(--grey);
    font-size: 2rem;
  }

  #reviews .reviews-imgs {
    flex-direction: column;
  }

  #reviews .reviews-imgs div {
    margin: 1rem;
    flex-direction: row;
    height: 2rem;
    width: 100%;
    padding: 0 1rem;
  }

  #reviews .reviews-imgs div img {
    display: block;
    max-width: 64px;
    max-height: 64px;
    height: 64px;
    width: 64px;
    border-radius: 50%;
    /* transfrom: translate3d(-50%, -50%, 0); */
    flex: 1 1;
  }

  #reviews .reviews-imgs p {
    flex: 1 1;
    color: var(--grey);
    background: none;
    text-align: start;
    padding: 0.5rem 0;
    margin-left: 0.5rem;
    width: 70%;
  }
}

#sponsors {
  text-align: center;
  margin-top: 5rem;
  /* border: blue 1px solid; */
}

#sponsors h2 {
  color: var(--grey);
  font-size: 3rem;
  margin-bottom: 1.5rem;
}

#sponsors .sponsors-imgs {
  padding: 1rem;
}

#sponsors .sponsors-imgs img {
  padding: 1rem 2rem;
}

@media (max-width: 768px) {
  #sponsors {
    margin-top: 5rem;
  }

  #sponsors h2 {
    font-size: 2rem;
  }

  #sponsors .sponsors-imgs {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
  }

  #sponsors .sponsors-imgs img {
    width: 33%;
    padding: 1rem;
  }
}

#pricing {
  background: url(/nordvpn-clone/static/media/header-showcase.dfe70d17.jpg) no-repeat center center/cover;
  height: 100vh;
  padding: 6rem 0;
}

#pricing .pricing-info {
  text-align: center;
}

#pricing .pricing-info h1 {
  font-size: 3rem;
  color: var(--white);
}

#pricing .pricing-info p {
  font-size: 1.1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  color: var(--white);
}

#pricing .pricing-card {
  display: flex;
  text-align: center;
  justify-content: center;
  padding: 0 12rem;
}

#pricing .pricing-card div {
  background: white;
  margin: 1rem 0.4rem;
  padding: 1rem 0.8rem;
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

#pricing .pricing-card div:first-child {
  border: 3px solid #4687ff;
}

#pricing .pricing-card div h3 span {
  display: inline-block;
  font-size: 1.5rem;
  color: var(--grey);
  vertical-align: top;
}

#pricing .pricing-card div h3 {
  font-size: 2.5rem;
  color: var(--grey);
  padding: 0.3rem 0;
}

#pricing .pricing-card div .special {
  display: inline-block;
  margin: 0.8rem 0;
  font-size: 0.9rem;
  padding: 0.1rem 0.6rem;
  border-radius: 25px;
  text-align: center;
  color: var(--grey);
  background: #e8e8e9;
}

#pricing .pricing-card div .best {
  color: #f64f64;
  background: #fddce0;
}

#pricing .pricing-card div .extra {
  color: #f64f64;
  font-size: 1.5rem;
  font-weight: 600;
}

#pricing .pricing-card .btn {
  display: block;
  width: 100%;
  color: var(--grey);
  padding: 0.5rem 0.8125rem;
  margin: 1rem 0rem;
  border: 1px solid var(--grey);
  border-radius: 3px;
}

#pricing .pricing-card .btn:hover {
  color: white;
  background: var(--grey);
}

#pricing .pricing-card .offer-btn {
  color: white;
  background: #4687ff;
  border: 1px solid #4687ff;
}

#pricing .pricing-card .offer-btn:hover {
  background: #4687ff;
  opacity: 0.9;
}

#pricing .pricing-card p .price-red {
  color: #f64f64;
  text-decoration: line-through;
}

#pricing .pricing-card div .mobile-price {
  display: none;
}

@media (max-width: 768px) {
  #pricing {
    padding: 4rem 0;
  }

  #pricing .pricing-info h1 {
    font-size: 2rem;
  }

  #pricing .pricing-info p {
    font-size: 1rem;
  }

  #pricing .pricing-card {
    flex-direction: column;
    padding: 0 1rem;
  }

  #pricing .pricing-card div {
    margin: auto;
    margin-bottom: 0.5rem;
    position: relative;
    padding-bottom: 0.5rem;
  }

  #pricing .pricing-card div p:first-child {
    position: absolute;
  }

  #pricing .pricing-card div .special {
    position: absolute;
    right: 1rem;
    top: 0;
  }

  #pricing .pricing-card div .extra {
    color: #f64f64;
    font-size: 1.1rem;
    font-weight: 600;
    margin-top: 1.5rem;
    padding: 0.2rem 0;
    text-align: left;
  }

  #pricing .pricing-card div .pc-price {
    display: none;
  }

  #pricing .pricing-card div .mobile-price {
    display: block;
    font-size: 1.5rem;
    text-align: left;
  }

  #pricing .pricing-card div .mobile-price-normal {
    padding-top: 2rem;
  }

  #pricing .pricing-card div p:last-child {
    display: none;
  }
}

@media (min-height: 900px) {
  #pricing {
    padding-top: 15rem;
  }
}

@media (max-width: 375px) {
  #pricing {
    padding-top: 1rem;
  }

  #pricing .pricing-info h1 {
    font-size: 2rem;
    color: var(--white);
  }

  #pricing .pricing-info p {
    font-size: 1rem;
    margin-top: 0rem;
    margin-bottom: 0.5rem;
    color: var(--white);
  }

  #pricing .pricing-card div {
    background: white;
    padding: 0.2rem 0.5rem;
    margin: 0.2rem 0;
    width: 100%;
    height: 100%;
    border-radius: 5px;
  }
}

#main-footer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 3rem;
  background: #151922;
}

#main-footer p {
  text-align: center;
  color: #73757a;
}

@media (max-width: 768px) {
  #main-footer p {
    font-size: 0.8rem;
  }
}

:root {
  --white: #fff;
  --grey: #333;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Roboto Condensed", sans-serif;
}

body {
  overflow-x: hidden;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

/* Utility Classes */

.m-heading {
  color: #333;
  color: var(--grey);
  font-size: 1.8rem;
}

.lead {
  color: #383c43;
  line-height: 1.8rem;
  font-size: 1.2rem;
}

.btn-secondary {
  background: #4687ff;
  color: #fff;
  color: var(--white);
  padding: 0.5rem 1.5rem;
  border-radius: 3px;
  font-weight: 600;
}

.btn-secondary:hover {
  background: #90b7ff;
}

/* Utility Classes Ends */

